"use client"

import { Button } from "@/components/ui/button"
import { useEffect, useState } from "react"

const backgroundImages = [
  "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Image%202025-11-04%20at%2017.02.53%20%284%29-KnMkvbTIvqdQH6A8TFJx7GVqbQrfH1.jpeg",
  "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Image%202025-11-04%20at%2017.02.51%20%285%29-PSBxuWNShbrGHhf5klDAZc5AAA86B3.jpeg",
  "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Image%202025-11-04%20at%2017.02.53%20%283%29-dURVQa0UduWiOYqtV9O4vFv71NNWjz.jpeg",
  "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Image%202025-11-04%20at%2017.02.52%20%284%29-tRBRTrPnn6leOUGcqtlqXx9r2ZwFyg.jpeg",
  "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Image%202025-11-04%20at%2017.02.52%20%285%29-qM803jNrIF8ktFRd6F10L8l3Qo6O9S.jpeg",
  "/f1.jpeg",
  "/f2.jpeg",
  "/f3.jpeg",
  "/f4.jpeg",
]

export function Hero() {
  const [currentImageIndex, setCurrentImageIndex] = useState(0)
  const [visible, setVisible] = useState(false)

  // --- Rotation automatique ---
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImageIndex((prev) => (prev + 1) % backgroundImages.length)
    }, 6000)
    return () => clearInterval(interval)
  }, [])

  // --- Apparition progressive au scroll ---
  useEffect(() => {
    const handleScroll = () => {
      const section = document.getElementById("accueil")
      if (section) {
        const rect = section.getBoundingClientRect()
        if (rect.top < window.innerHeight * 0.8) setVisible(true)
      }
    }
    window.addEventListener("scroll", handleScroll)
    handleScroll()
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <section
      id="accueil"
      className="relative text-white overflow-hidden h-[700px] md:h-[800px] flex items-center justify-center"
    >
      {/* --- Images de fond avec fondu --- */}
      {backgroundImages.map((image, index) => (
        <div
          key={index}
          className={`absolute inset-0 bg-cover bg-center transition-opacity duration-[1500ms] ease-in-out ${
            currentImageIndex === index ? "opacity-100 scale-100" : "opacity-0 scale-105"
          }`}
          style={{ backgroundImage: `url('${image}')` }}
        />
      ))}

      {/* --- Dégradé sombre --- */}
      <div className="absolute inset-0 bg-gradient-to-b from-black/80 via-black/50 to-black/80" />

      {/* --- Contenu principal --- */}
      <div
        className={`relative z-10 text-center px-6 max-w-3xl transition-all duration-1000 ${
          visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"
        }`}
      >
        <h1
          className={`text-4xl md:text-6xl lg:text-7xl font-extrabold mb-6 leading-tight tracking-tight transition-all duration-1000 delay-200 ${
            visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
          }`}
        >
          Construisons ensemble <br />
          <span className="text-primary">l'avenir de vos projets</span>
        </h1>

        <p
          className={`text-lg md:text-xl mb-10 text-gray-200 leading-relaxed transition-all duration-1000 delay-500 ${
            visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
          }`}
        >
          Expert en construction métallique, installations solaires et infrastructures.
          <br className="hidden md:block" /> Nous transformons vos idées en réalité avec qualité et innovation.
        </p>

        <div
          className={`transition-all duration-1000 delay-700 ${
            visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
          }`}
        >
          <Button
            size="lg"
            variant="outline"
            className="border-2 border-primary text-white hover:bg-primary/25 bg-transparent backdrop-blur-sm font-semibold px-8 py-6 text-lg transition-all duration-500 hover:scale-105 hover:shadow-[0_0_20px_rgba(255,255,255,0.4)]"
            onClick={() =>
              document.getElementById("services")?.scrollIntoView({ behavior: "smooth" })
            }
          >
            Découvrez nos services
          </Button>
        </div>
      </div>

      {/* --- Indicateurs --- */}
      <div className="absolute bottom-10 left-1/2 -translate-x-1/2 z-10 flex gap-3">
        {backgroundImages.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentImageIndex(index)}
            className={`h-3 w-3 rounded-full transition-all duration-300 ${
              currentImageIndex === index
                ? "bg-primary scale-125 shadow-[0_0_10px_rgba(255,255,255,0.9)]"
                : "bg-white/40 hover:bg-white/70"
            }`}
            aria-label={`Image ${index + 1}`}
          />
        ))}
      </div>
    </section>
  )
}
